<template>
  <el-card>
    <div class="title">
      <img src="../../assets/cloud-upload.png" />
      <h3>xlsx</h3>
    </div>
    <el-divider></el-divider>
    <div class="body">
      <div>
        <span></span>

        <el-radio v-model="system" label="" border
          >2018-2020</el-radio
        >
        <el-radio v-model="system" label="" border
          ></el-radio
        >
      </div>
      <el-divider></el-divider>
      <el-divider></el-divider>
      <el-upload
        ref="uploadElement"
        drag
        action="none"
        multiple
        :show-file-list="false"
        :auto-upload="false"
        :on-change="checkFileType"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
      </el-upload>
    </div>
    <div class="dialog">
      <el-dialog
        title=""
        :visible.sync="isfailToUpload"
        width="400px"
      >
        <el-table :data="failToUpload">
          <el-table-column
            property="filename"
            label=""
            width="150"
          ></el-table-column>
          <el-table-column
            property="reason"
            label=""
            width="150"
          ></el-table-column>
        </el-table>
      </el-dialog>
    </div>
  </el-card>
</template>

<script>
import { upload } from '@/api/file'
export default {
  data() {
    return {
      filelist: [],
      failToUpload: [],
      isfailToUpload: false,
      system: ''
    }
  },
  methods: {
    // filefilelist
    checkFileType(file, filelist) {
      // 
      const fileType = file.name.substring(file.name.lastIndexOf('.') + 1)
      if (fileType !== 'xls' && fileType !== 'xlsx' && fileType !== 'csv') {
        alert('.xls.xlsx.csv')
        // 
        filelist = filelist.pop()
        return false
      }
      this.file = file
      this.filelist = filelist
      this.uploadFile()
      this.$refs.uploadElement.clearFiles()
    },
    uploadFile() {
      //
      this.filelist.forEach(item => {
        const formData = new FormData()
        formData.append('file', item.raw)
        const config = {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('token')
          }
        }
        upload(this.system, formData, config)
          .then(res => {
            if (res.data.code === 200) {
              this.$message.success(`${item.name} `)
            } else if (res.data.code === 400) {
              this.$message.warning(`${item.name} ${res.data.msg}`)
              this.isfailToUpload = true
              this.failToUpload.push({
                filename: item.name,
                reason: res.data.msg
              })
            } else {
              this.$message.warning(res.data.msg)
            }
          })
          .catch(err => {
            this.$message.error(err.message)
          })
      })

      //  axios
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  width: 1022px;
  background-color: #f2f6fc;
  margin: 0;
  border: 1px solid #dcdfe6;
  .title {
    display: flex;
    align-items: center;
    padding-bottom: 0;
    height: 24px;
    img {
      width: 24px;
    }
    h3 {
      line-height: 24px;
      padding-left: 10px;
    }
  }
  .body {
    /deep/.el-upload-dragger {
      width: 984px;
    }
    .el-radio {
      margin-left: 50px;
      margin-right: 0;
    }
  }
  .el-select {
    width: 70%;
  }
  .el-button {
    float: right;
  }
}
</style>
