<template>
  <div class="container" ref="line"></div>
</template>

<script>
export default {
  data() {
    return {
      graph: null,
      myChart: null
    }
  },
  props: ['lineData', 'indexOfTime', 'labelList', 'index'],
  methods: {
    draw() {
      this.graph = this.$refs.line
      this.myChart = this.$echarts.init(this.graph)
      const options = {
        dataset: [
          {
            source: this.lineData
          }
        ],
        title: {
          text: this.labelList[this.index],
          left: 'center',
          textStyle: {
            fontFamily: 'SimSun'
          }
        },
        // 
        tooltip: {
          // 
          trigger: 'axis'
        },
        grid: {
          top: '10%',
          left: '10%',
          right: '3%',
          bottom: '15%',
          show: false, // 
          borderColor: '#012f4a', // 
          containLabel: true // 
        },
        // 
        toolbox: {
          feature: {
            // 
            saveAsImage: {}
          },
          right: '2%'
        },
        xAxis: {
          // 
          type: 'time',
          // 
          boundaryGap: false
        },
        yAxis: {
          type: 'value',
          min: 'dataMin'
        },
        dataZoom: [
          {
            type: 'inside',
            start: 0,
            end: 100
          },
          {
            height: '20',
            start: 0,
            end: 100
          }
        ],
        series: {
          type: 'line',
          // 
          symbol: 'none',
          //  Largest-Triangle-Three-Bucket 
          sampling: 'lttb',

          lineStyle: {
            width: 1
          },
          name: this.labelList[this.index],
          encode: {
            x: this.indexOfTime,
            y: this.indexOfTime === 0 ? 1 : 0
          },
          areaStyle: {
            color: new this.$echarts.graphic.LinearGradient(1, 0, 0, 0, [
              {
                offset: 0,
                color: `rgb(${Math.floor(
                  Math.random() * 128 + 128
                )}, ${Math.floor(Math.random() * 128 + 128)}, ${Math.floor(
                  Math.random() * 128 + 128
                )})`
              },
              {
                offset: 1,
                color: `rgb(${Math.floor(
                  Math.random() * 128 + 128
                )}, ${Math.floor(Math.random() * 128 + 128)}, ${Math.floor(
                  Math.random() * 128 + 128
                )})`
              }
            ])
          }
        }
      }
      this.myChart.setOption(options, true)
      window.addEventListener('resize', () => {
        this.myChart.resize()
      })
    }
  },
  mounted() {
    this.draw()
    this.$emit('drawed', false)
  },
  activated() {
    this.myChart.resize()
  }
}
</script>

<style lang="less" scoped></style>
